.about-page {
    &__content {
        max-width: 1420px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px 70px;
    }

    &__inner {
        display: flex;
        align-items: flex-start;
        gap: 40px;
        max-width: 1280px;
    }

    &__title {
        color: rgb(0, 0, 0);
        font-family: Oswald;
        font-size: 40px;
        font-weight: 600;
        line-height: 140%;
        margin: 0 0 44px;
    }

    &__text {
        color: rgb(0, 0, 0);
        font-family: Roboto;
        font-size: 18px;
        font-weight: 400;
        line-height: 140%;
        margin: 0 0 20px;
        max-width: 680px;
    }

    &__image {
        height: 450px;
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @media screen and (max-width: 1890px) {
        &__inner {
            max-width: 1200px;
        }
    }

    @media screen and (max-width: 1750px) {
        &__inner {
            max-width: 1100px;
        }
    }

    @media screen and (max-width: 1550px) {
        &__inner {
            max-width: 1000px;
        }
    }

    @media screen and (max-width: 1380px) {
        .news-block {
            display: none;
        }

        &__inner {
            max-width: 100%;
        }
    }

    @media screen and (max-width: 1190px) {
        &__inner {
            gap: 20px;
        }

        &__text {
            max-width: 550px;
        }
    }

    @media screen and (max-width: 1024px) {
        &__inner {
            flex-direction: column-reverse;
            gap: 20px;
        }

        &__image {
            height: 100%;
        }

        &__title,
        &__text,
        &__desc {
            max-width: 100%;
        }
    }

    @media screen and (max-width: 768px) {
        &__content {
            padding: 0 45px 30px;
        }

        &__title {
            font-size: 30px;
            margin-bottom: 20px;
        }

        &__text {
            margin-bottom: 20px;
        }

    }

    @media screen and (max-width: 390px) {
        &__content {
            padding: 0 35px 40px;
        }

        &__title {
            font-size: 22px;
        }

        &__text {
            font-size: 16px;
            margin-bottom: 30px;
        }
    }

