.service {
    position: relative;
    background-image: url(../img/content/slash-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom left;

    &__content {
        max-width: 1420px;
        width: 100%;
        margin: 0 auto;
        padding: 80px 40px;
        position: relative;
    }

    &__jivo-link {
        z-index: 99;
    }

    &__sticker-mobile {
        display: none;
    }

    &__title {
        color: rgb(0, 0, 0);
        font-family: Oswald;
        font-size: 40px;
        font-weight: 600;
        line-height: 140%;
        margin: 0 0 44px;
    }

    &__slider {
        max-width: 1020px;
        height: 100%;
        margin: 0 0 0 70px;
        padding: 10px;
    }

    &__list {
        display: flex;
    }

    &__item {
        max-width: 320px;
        width: 100%;
        transition: .2s;
        height: auto;
    }

    /*li {
        border-left: 0.2px solid rgba(104, 98, 98, 0.1);
        border-right: 0.2px solid rgba(104, 98, 98, 0.1);
    }*/

    .swiper-slide {
        box-shadow: 4px 4px 10px 0px rgba(122, 120, 120, 0.3);
    }

    &__item:hover {
        transform: translateY(-5px);
    }

    &__slide-link {
        height: 100%;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        margin: 0;
        padding: 30px 20px;
        background: rgb(255, 255, 255);
    }

    &__slide-content {
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;
    }

    &__slide-title {
        color: rgb(0, 0, 0);
        font-family: Roboto;
        font-size: 22px;
        font-weight: 700;
        line-height: 130%;
        margin: 0 0 30px;
    }

    &__slide-text {
        color: rgb(0, 0, 0);
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        margin: 0 0 10px;
    }

    &__slide-btn {
        max-width: 280px;
        width: 100%;
        background: rgb(0, 174, 0);
        color: rgb(255, 255, 255);
        font-family: Roboto;
        font-size: 16px;
        font-weight: 700;
        line-height: 140%;
        padding: 13px;
        margin-top: auto;
    }

    &__item:hover &__slide-btn {
        background: rgb(0, 194, 0);
    }

    .swiper-button-next,
    .swiper-button-prev {
        top: 56%;
    }

    .swiper-button-next {
        right: 200px;
    }

    .swiper-button-next::after {
        font-size: 12px;
        font-weight: 700;
        color: black;
        border: 1px solid black;
        border-radius: 50%;
        padding: 9px 13px 9px 12px;
        background: transparent;
    }

    .swiper-button-next:hover {
        opacity: 0.5;
    }

    .swiper-button-prev {
        left: 20px;
    }

    .swiper-button-prev::after {
        font-size: 12px;
        font-weight: 700;
        color: black;
        border: 1px solid black;
        border-radius: 50%;
        padding: 9px 13px 9px 12px;
        background: transparent;
    }

    .swiper-button-prev:hover {
        opacity: 0.5;
    }

    @media screen and (max-width: 1790px) {
        &__slider {
            max-width: 990px;
        }

        .swiper-button-prev {
            left: 50px;
        }

        .swiper-button-next {
            left: 1150px;
        }
    }

    @media screen and (max-width: 1600px) {

        .swiper-button-prev {
            left: 70px;
        }

        .swiper-button-next {
            left: 1110px;
        }
    }

    @media screen and (max-width: 1540px) {
        &__slider {
            max-width: 950px;
        }

        .swiper-button-next {
            left: 1080px;
        }
    }

    @media screen and (max-width: 1470px) {
        &__slider {
            max-width: 900px;
            margin-left: 40px;
        }

        .swiper-button-prev {
            left: 40px;
        }

        .swiper-button-next {
            left: 990px;
        }
    }

    @media screen and (max-width: 1452px) {
        &__slider {
            max-width: 690px;
            margin-left: 120px;
        }

        .swiper-button-prev {
            left: 100px;
        }

        .swiper-button-next {
            left: 880px;
        }
    }

    @media screen and (max-width: 1280px) {

        &__slider {
            margin: 0 auto;
            max-width: 990px;
        }

        .swiper-button-prev {
            left: calc(50% - 530px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 530px);
        }
    }

    @media screen and (max-width: 1170px) {
        &__slider {
            margin: 0 auto;
            max-width: 900px;
        }

        .swiper-button-prev {
            left: calc(50% - 490px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 490px);
        }

    }

    @media screen and (max-width: 1031px) {
        &__slider {
            margin: 0 auto;
            max-width: 685px;
        }

        .swiper-button-prev {
            left: calc(50% - 400px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 400px);
        }

    }

    @media screen and (max-width: 920px) {

        &__slider {
            margin: 0 auto;
            max-width: 640px;
            width: 100%;
        }

        .swiper-button-prev {
            left: calc(50% - 350px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 350px);
        }
    }

    @media screen and (max-width: 768px) {
        &__content {
            padding: 50px 45px;
        }

        &__title {
            font-size: 32px;
            margin-bottom: 30px;
        }
    }

    @media screen and (max-width: 740px) {
        &__slider {
            max-width: 320px;
        }

        .swiper-button-prev {
            left: calc(50% - 200px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 200px);
        }
    }

    @media screen and (max-width: 730px) {
        &__jivo-link {
            display: none;
        }
    }

    @media screen and (max-width: 480px) {
        &__title {
            font-size: 24px;
        }
    }

    @media screen and (max-width: 405px) {
        .swiper-button-prev {
            left: 10px;
        }

        .swiper-button-next {
            left: auto;
            right: 10px;
        }
    }

    @media screen and (max-width: 390px) {
        &__content {
            padding: 40px 35px;
        }
    }
}