.partners {
    background: rgb(248, 248, 248);

    .service__content {
        max-width: 1420px;
        width: 100%;
        margin: 0 auto;
        padding: 80px 40px;
        position: relative;
    }

    .service__title {
        color: rgb(0, 0, 0);
        font-family: Oswald;
        font-size: 40px;
        font-weight: 600;
        line-height: 140%;
        margin: 0 0 44px;
    }

    .service__title>span {
        color: rgb(0, 174, 0);
    }

    .swiper-button-next,
    .swiper-button-prev {
        top: 57%;
    }

    .service__slider {
        max-width: 1020px;
        height: 100%;
        margin: 0 0 0 70px;
        padding: 10px 10px 10px 10px;
    }

    .service__list {
        display: flex;
    }

    .service__item {
        max-width: 320px;
        width: 100%;
        transition: .2s;
        height: auto;
    }

    .swiper-slide {
        box-shadow: 3px 3px 12px rgba(145, 145, 145, 0.3);
    }

    li:hover {
        box-shadow: 0px 0px 10px rgba(145, 145, 145, 0.3);
        transform: scale(0.98);
    }

    /*li:hover>&__slide-link>&__slide-content>&__slide-btn {
        background: rgb(0, 194, 0);
    }*/


    .service__slide-link {
        height: 100%;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        margin: 0;
        padding: 0;
        background: rgb(255, 255, 255);
    }

    .service__image {
        height: 123px;
        padding: 12px 8px 0;
        margin-bottom: 20px;
    }

    .service__slide-content {
        display: flex;
        flex-direction: column;
        padding: 0 20px 40px;
        margin-bottom: 0;
    }

    .service__slide-title {
        color: rgb(0, 0, 0);
        font-family: Roboto;
        font-size: 20px;
        font-weight: 700;
        line-height: 140%;
        margin: 0 0 10px;
    }

    .service__slide-text {
        color: rgb(128, 128, 128);
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        margin: 0 0 30px;
    }

    .service__slide-phone,
    .service__slide-email {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;
        margin: 0;

        span {
            color: rgb(0, 174, 0);
        }
    }

    .service__slide-phone {
        margin-bottom: 3px;
    }

    .service__slide-email>span {
        text-decoration: underline;
    }

    .swiper-button-next {
        right: 200px;
    }

    .swiper-button-next::after {
        font-size: 12px;
        font-weight: 700;
        color: black;
        border: 1px solid black;
        border-radius: 50%;
        padding: 9px 13px 9px 12px;
        background: transparent;
    }

    .swiper-button-next:hover {
        opacity: 0.5;
    }

    .swiper-button-prev {
        left: 20px;
    }

    .swiper-button-prev::after {
        font-size: 12px;
        font-weight: 700;
        color: black;
        border: 1px solid black;
        border-radius: 50%;
        padding: 9px 13px 9px 12px;
        background: transparent;
    }

    .swiper-button-prev:hover {
        opacity: 0.5;
    }

    @media screen and (max-width: 1790px) {

        .swiper-button-prev {
            left: 50px;
        }

        .swiper-button-next {
            left: 1150px;
        }
    }

    @media screen and (max-width: 1600px) {
        .service__slider {
            max-width: 990px;
        }

        .swiper-button-prev {
            left: 70px;
        }

        .swiper-button-next {
            left: 1110px;
        }
    }

    @media screen and (max-width: 1540px) {
        .service__slider {
            max-width: 950px;
        }

        .swiper-button-next {
            left: 1080px;
        }
    }

    @media screen and (max-width: 1470px) {
        .service__slider {
            max-width: 900px;
            margin-left: 30px;
        }

        .swiper-button-prev {
            left: 30px;
        }

        .swiper-button-next {
            left: 980px;
        }
    }

    @media screen and (max-width: 1452px) {
        .service__slider {
            max-width: 690px;
            margin-left: 120px;
        }

        .swiper-button-prev {
            left: 100px;
        }

        .swiper-button-next {
            left: 880px;
        }
    }


    @media screen and (max-width: 1280px) {

        .service__slider {
            margin: 0 auto;
            max-width: 990px;
        }

        .swiper-button-prev {
            left: calc(50% - 530px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 530px);
        }
    }

    @media screen and (max-width: 1170px) {
        .service__slider {
            margin: 0 auto;
            max-width: 900px;
        }

        .swiper-button-prev {
            left: calc(50% - 490px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 490px);
        }

    }

    @media screen and (max-width: 1031px) {
        .service__slider {
            margin: 0 auto;
            max-width: 685px;
        }

        .swiper-button-prev {
            left: calc(50% - 400px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 400px);
        }

    }

    @media screen and (max-width: 920px) {

        .service__slider {
            margin: 0 auto;
            max-width: 640px;
            width: 100%;
        }

        .swiper-button-prev {
            left: calc(50% - 360px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 360px);
        }
    }

    @media screen and (max-width: 768px) {
        .service__content {
            padding: 50px 45px;
        }

        .service__title {
            font-size: 32px;
            margin-bottom: 30px;
        }
    }

    @media screen and (max-width: 740px) {
        .service__slider {
            max-width: 320px;
        }

        .swiper-button-prev {
            left: calc(50% - 200px);
        }

        .swiper-button-next {
            left: auto;
            right: calc(50% - 200px);
        }
    }

    @media screen and (max-width: 480px) {
        .service__title {
            font-size: 24px;
        }
    }

    @media screen and (max-width: 405px) {
        .swiper-button-prev {
            left: 10px;
        }

        .swiper-button-next {
            left: auto;
            right: 10px;
        }
    }

    @media screen and (max-width: 390px) {
        .service__content {
            padding: 40px 35px;
        }
    }
}